.feedback-form {
  max-width: 480px;
  background-color: #fff;
  border: 1px solid #0000001f;
  border-radius: 4px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  padding: 8px;
}

textarea {
  resize: none;
}

.js-age {
  color: #1a1a1e;
  margin: 0 auto;
  padding: 0;
  font-family: Trebuchet MS, Lucida Sans Unicode, Lucida Grande, Lucida Sans, Arial, sans-serif;
  font-size: 25px;
  font-weight: 900;
}

